import ACQueryBuilder from "./modules/ac-query-builder.js";
import Segments from "./modules/segments.js";
import WhitelistedUrlParams from "./modules/segments/whitelisted-url-params";
import $ from 'jquery';

global.AdminColumns = typeof AdminColumns !== "undefined" ? AdminColumns : {};

$( document ).ready( function() {
	if( ! AC.layout ){
		return;
	}

	if ( $( '#ac-s' ).length && ac_search.filters.length ) {
		AdminColumns.Search = new ACQueryBuilder( '#ac-s', ac_search );
		AdminColumns.Search.init();

		if ( document.querySelector( '.ac-segments' ) ) {
			AdminColumns.Segments = new Segments(
				AdminColumns.Table,
				AdminColumns.Search,
				ACP_Sorting,
				new WhitelistedUrlParams( document.querySelector( '#ac-s' ) )
			);
		}

		$( '[name="acp_filter_action"], #post-query-submit' ).addClass( 'ac-search-button' );
	}

	// If Search is disabled, segments can't work
	if ( !document.getElementById( 'ac-s' ) && AdminColumns.Table && AdminColumns.Table.Actions ) {

		$( '.ac-table-button.-segments' ).remove();
		AdminColumns.Table.Actions.refresh();
	}

	$( document ).on( 'wheel', '.ui-datepicker-year', function( e ) {
		e.preventDefault();

		if ( e.originalEvent.deltaY > 1 ) {
			$( this ).find( 'option:selected' ).next().prop( 'selected', true ).trigger( 'change' );
		}

		if ( e.originalEvent.deltaY < -1 ) {
			$( this ).find( 'option:selected' ).prev().prop( 'selected', true ).trigger( 'change' );
		}
	} );

} );
