const action = 'acp_search_segment_request';
const axios = require( 'axios' );

const deleteSegment = ( name ) => {
	return jQuery.ajax( {
		url : ajaxurl,
		method : 'post',
		data : {
			_ajax_nonce : AC.ajax_nonce,
			action : action,
			list_screen : AC.list_screen,
			layout : AC.layout,
			name : name,
			method : 'delete',
		}
	} );
};

const retrieveSegments = () => {
	return jQuery.ajax( {
		url : ajaxurl,
		method : 'post',
		data : {
			_ajax_nonce : AC.ajax_nonce,
			action : action,
			list_screen : AC.list_screen,
			layout : AC.layout,
			name : name,
			method : 'read',
		}
	} );
};

const saveSegment = ( name, querystring, whitelisted ) => {
	let data = {
		_ajax_nonce : AC.ajax_nonce,
		action : action,
		layout : AC.layout,
		method : 'create',
	}

	let formData = new FormData();

	for ( let key in data ) {
		formData.append( key, data[ key ] );
	}

	let whitelisted_qs = new URLSearchParams();

	whitelisted.forEach( k => {
		whitelisted_qs.append( k, '' )
	} );

	formData.append( 'name', name );
	formData.append( 'query_string', querystring );
	formData.append( 'whitelisted_query_string', whitelisted_qs );

	return axios.post( ajaxurl, formData )
};

export {deleteSegment, retrieveSegments, saveSegment}