export default class WhitelistedUrlParams {

	constructor( element ) {
		this.element = element;
		this.queryParams = new URLSearchParams( window.location.search );
	}

	hasUrlParameter( key ) {
		return null !== this.queryParams.get( key );
	}

	/**
	 * Return filters that are not managed by Admin Columns but are active
	 */
	getWhitelistedParams() {
		let params = [
			'ac-rules', 'orderby', 'order', 's', 'post_status', 'comment_status'
		];

		let allowedTagNames = ['SELECT', 'INPUT'];

		let siblings = Array.prototype.filter.call( this.element.parentNode.children, ( sibling ) => {
			return allowedTagNames.includes( sibling.tagName ) && sibling !== this.element;
		} );

		siblings.forEach( ( element ) => {
			let name = element.getAttribute( 'name' );

			if ( this.hasUrlParameter( name ) ) {
				params.push( name );
			}
		} );

		// Specific ACP logic
		this.element.parentNode.querySelectorAll( '.acp-range input' ).forEach( input => {
			let name = input.getAttribute( 'name' );

			if ( this.hasUrlParameter( name ) ) {
				params.push( name );
			}
		} );

		return params;
	}

}